<template>
  <div class="allow-insurance-type">
    <a-card>
    <div class="checkbox-container">
      <div class="checkbox-header">
        <a-checkbox
          :indeterminate="indeterminate"
          @change="onCheckAllChange"
          :checked="checkAll"
        >
          选择全部
        </a-checkbox>
      </div>
      <a-checkbox-group
        :options="plainOptions"
        v-model="checkedList"
        @change="onChange"
      />
      </div>
      <div class="tags-container">
        <strong :style="{ marginRight: 8 }">已选险种:</strong>
        <template v-for="tag in tags">
          <a-tag
            class="ant-tag-checkable ant-tag-checkable-checked"
            :key="tag"
            :afterClose="() => handleClose(tag)"
          >
            {{tag}}
          </a-tag>
        </template>
      </div>
    </a-card>
  </div>
</template>

<script>
import ACard from "ant-design-vue/lib/card";
import ATag from "ant-design-vue/lib/tag";
import ACheckbox from "ant-design-vue/lib/checkbox";
const ACheckboxGroup = ACheckbox.Group;
const plainOptions = [
  "车辆损失险",
  "第三方责任险",
  "全车盗抢险",
  "车上人员责任险(司机)",
  "车上人员责任险(乘客)",
  "车身划痕险",
  "自燃损失险",
  "玻璃单独破碎险",
  "新增设备损失险",
  "发动机涉水损失险",
  "指定专修厂",
  "修理期间费用补偿险",
  "第三方特约险",
  "第三方节假日翻倍险",
  "交强险",
  "自定义险1",
  "自定义险2"
];
const defaultCheckedList = [];
export default {
  components: {
    ACard,
    ATag,
    ACheckbox,
    ACheckboxGroup
  },
  data() {
    return {
      tags: ["请按需求选择以下险种"],
      plainOptions,
      checkedList: defaultCheckedList,
      indeterminate: true,
      checkAll: false
    };
  },
  watch: {
    checkedList: {
      handler: function(checkedList) {
          if(checkedList.length){
              this.tags = checkedList;
          }else{
              this.tags = ["请按需求选择以下险种"]
          }
        
      }
    }
  },
  methods: {
    handleClose(removedTag) {
      const tags = this.tags.filter(tag => tag !== removedTag);
      console.log(tags);
      this.tags = tags;
    },
    onChange(checkedList) {
      this.indeterminate =
        !!checkedList.length && checkedList.length < plainOptions.length;
      this.checkAll = checkedList.length === plainOptions.length;
    },
    onCheckAllChange(e) {
      Object.assign(this, {
        checkedList: e.target.checked ? plainOptions : [],
        indeterminate: false,
        checkAll: e.target.checked
      });
    }
  }
};
</script>

<style scoped>
.allow-insurance-type .checkbox-container{
    margin-bottom: 16px
}
.allow-insurance-type .tags-container > div {
  margin-bottom: 16px;
  font-size: 14px;
}
.allow-insurance-type .checkbox-header {
  border-bottom: 1px solid #e9e9e9;
  padding: 16px 0;
  margin-bottom: 16px;
}
</style>

<style>
.allow-insurance-type .ant-checkbox-group label.ant-checkbox-group-item {
  margin-bottom: 16px;
  margin-right: 0;
}

@media (min-width: 1550px) {
  .allow-insurance-type .ant-checkbox-group label.ant-checkbox-group-item {
    width: 16.66%;
  }
}

@media (min-width: 900px) and (max-width: 1550px) {
  .allow-insurance-type .ant-checkbox-group label.ant-checkbox-group-item {
    width: 25%;
  }
}

@media (min-width: 450px) and (max-width: 900px) {
  .allow-insurance-type .ant-checkbox-group label.ant-checkbox-group-item {
    width: 50%;
  }
}

@media (max-width: 450px) {
  .allow-insurance-type .ant-checkbox-group label.ant-checkbox-group-item {
    width: 100%;
  }
}
</style>





